import axios from 'axios'
import Qs from 'qs'
// const base = window.location.href.indexOf("localhost") >= 0 ? 
// 'http://www.yueshijue.com' : window.location.protocol + "//" + window.location.host;
// axios.defaults.headers.common['Authorization'] = localStorage.getItem('sessionId')
// axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'
// axios.defaults.transformRequest = [(data) => { return Qs.stringify(data) }]
axios.defaults.baseURL = ''
axios.defaults.headers['X-Requested-With'] = 'XMLHttpRequest'
axios.defaults.headers.post['Content-Type'] = 'application/json'
axios.defaults.transformRequest = [data => {
  // console.log(JSON.stringify(data))
  return JSON.stringify(data)
}]
axios.defaults.timeout = 5000

// 登录
export const requestLogin = data => { return axios.post('/login', data) }
// 登出
export const requestExit = () => { return axios.get('/logout') }
// 获取用户权限
export const getPermissionList = () => { return axios.get('/ysj/permission/getPermissionList.json')}
// 注册
export const requestRegister = data => { return axios.post('/register', data) }

// 获取用户信息
export const getMyinfo = () => {return axios.get('/accountInter/getMyinfo.do')}

// 更新用户信息
export const updateMyInfo = data => {return axios.post('/accountInter/updateMyInfo.do', data)}

// 获取用户商家信息
export const getMyPartner = () => {return axios.get('/accountInter/getMyPartner.do')}

// 更新密码
export const updatePwd = data => {return axios.post('/accountInter/updatePwd.do', data)}

// 新建商家信息
export const createPartner = data => {return axios.post('/accountInter/createPartner.do', data)}

// 获取用户列表
export const getUserList = params => {return axios.get('/user/list', {params: params})}

// 获取企业组织部门树
export const readOrganizeTree = () => {return axios.get('/partner/readOrganizeTree')}
// 创建/编辑组织部门
export const saveOrganizeTree = data => {return axios.post('/partner/saveOrganizeTree', data)}
// 删除部门
export const deleteOrganize = data => {return axios.post('/partner/deleteOrganize', data)}
// 部门状态设置
export const setOrganizeStatus = data => {return axios.post('/partner/setOrganizeStatus', data)}
// 从部门移除员工
export const setUserOrg = data => {return axios.post('/partner/setUserOrg', data)}
// 删除员工
export const removeUser = data => {return axios.post('/partner/removeUser', data)}

// 获取某个子系统功能树的接口
export const getFunctionTree = () => {return axios.get('/adminInter/getFunctionTree.do')}
// 获取单个功能点信息的接口
export const getFunctionNode = params => {return axios.get('/adminInter/getFunctionNode.do', {params: params})}
// 新建功能点
export const createFunction = data => {return axios.post('/adminInter/createFunction.do', data)}
// 更新功能点
export const updateFunction = data => {return axios.post('/adminInter/updateFunction.do', data)}
// 删除功能点
export const delFunction = data => {return axios.post('/adminInter/delFunction.do', data)}


